/* Complete home.css - Clean and Organized Version */

/* ===== GLOBAL STYLES ===== */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
}

#root {
  min-height: 100vh;
  overflow-x: hidden;
}

/* Global underline removal */
* {
  text-decoration: none;
}

a, button, input, textarea, select {
  text-decoration: none;
}

a:hover, a:focus, a:visited, a:active,
button:hover, button:focus, button:visited, button:active {
  text-decoration: none;
}

/* ===== CONTAINER & LAYOUT ===== */
.home-container {
  min-height: 100vh;
  width: 100%;
  position: relative;
  transition: all 0.5s ease;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
}

/* ===== BACKGROUND ===== */
.home-background-image {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  opacity: 1;
  z-index: 0;
  pointer-events: none;
  animation: backgroundFloat 20s ease-in-out infinite;
}

@keyframes backgroundFloat {
  0%, 100% {
    transform: scale(1) translate(0, 0);
  }
  25% {
    transform: scale(1.02) translate(-2px, -1px);
  }
  50% {
    transform: scale(1.01) translate(1px, -2px);
  }
  75% {
    transform: scale(1.03) translate(-1px, 1px);
  }
}

/* ===== HEADER & NAVIGATION ===== */
.home-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
  padding: 1.5rem 1.5rem 1.5rem 0.5rem;
}

.home-header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 80rem;
  margin: 0 auto;
}

.home-logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.home-logo-image {
  width: 5rem;
  height: 5rem;
  object-fit: contain;
  border-radius: 0.5rem;
  transition: transform 0.3s ease;
}

.home-logo-image:hover {
  transform: scale(1.05);
}

.home-navigation {
  display: flex;
  align-items: center;
  gap: 2rem;
  background: rgba(130, 130, 130, 0.3);
  backdrop-filter: blur(10px);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 9999px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.home-nav-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 9999px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.875rem;
  font-weight: 500;
  text-decoration: none;
  outline: none;
}

.home-nav-button:hover,
.home-nav-button.active {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  text-decoration: none;
}

.home-nav-button:focus,
.home-nav-button:visited,
.home-nav-button:active {
  text-decoration: none;
  outline: none;
}

.home-dot {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
}

.blue-dot {
  background-color: #e26f8adf;
}

.pink-dot {
  background-color: #C1B6BA;
}

.yellow-dot {
  background-color: #6d96ec;
}

/* ===== SCROLL INDICATOR ===== */
/* Using unified about-scroll-indicator from about.css */

/* ===== SOCIAL ICONS ===== */
.home-side-navigation {
  position: fixed;
  bottom: 1.5rem;
  right: 1.5rem;
  left: auto;
  z-index: 99999;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  pointer-events: auto;
}

.home-social-icon {
  display: block;
  padding: 0.75rem;
  background: rgba(55, 65, 81, 0.3);
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: white;
  border-radius: 0.5rem;
  transition: all 0.3s ease;
  transform: scale(1);
  text-decoration: none;
  z-index: 99999;
  pointer-events: auto;
}

.home-social-icon:hover {
  transform: scale(1.1);
  text-decoration: none;
}

.home-social-icon:focus,
.home-social-icon:visited,
.home-social-icon:active {
  text-decoration: none;
}

.home-social-icon.linkedin:hover {
  background: rgba(37, 99, 235, 0.6);
  backdrop-filter: blur(15px);
}

.home-social-icon.email:hover {
  background: rgba(5, 150, 105, 0.6);
  backdrop-filter: blur(15px);
}

.home-social-icon.instagram:hover {
  background: rgba(219, 39, 119, 0.6);
  backdrop-filter: blur(15px);
}

/* ===== MAIN CONTENT ===== */
.home-main {
  position: relative;
  z-index: 10;
  width: 100%;
  min-height: 100vh;
}

/* ===== HERO SECTION ===== */
.home-hero-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 1.5rem;
  padding-left: 4rem;
}

.home-hero-content {
  text-align: left;
  max-width: 64rem;
  margin: 0;
}

.home-designer-label {
  margin-bottom: 2rem;
}

.home-designer-label span {
  color: #e26f8adf;
  font-size: 0.875rem;
  font-weight: 500;
  letter-spacing: 0.1em;
}

/* ===== TITLE STYLES ===== */
.home-title {
  margin: 0 0 2rem 0;
  color: white;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
  font-size: 4rem;
  font-weight: bold;
}

.home-title-line {
  line-height: 1;
  margin: 0;
  padding: 0;
  height: 1.2em;
  display: flex;
  align-items: center;
}

/* Comma on the first line (so punctuation doesn't affect line box) */
.home-title-line:first-child::after {
  content: ",";
}

.home-description {
  font-size: 1.125rem;
  color: #d1d5db;
  margin-bottom: 3rem;
  max-width: 32rem;
  margin-left: 0;
  margin-right: auto;
}

/* ===== CTA BUTTONS ===== */
.home-cta-buttons {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.home-cta-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background-color: #e26f8adf;
  color: white;
  padding: 1rem 2rem;
  border-radius: 9999px;
  font-size: 1.125rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  transform: scale(1);
  text-decoration: none;
}

.home-cta-button:hover {
  background-color: #000000;
  transform: scale(1.05);
  text-decoration: none;
}

.home-cta-button:focus,
.home-cta-button:visited,
.home-cta-button:active {
  text-decoration: none;
}

.home-cta-button-secondary {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(45, 55, 72, 0.1);
  color: white;
  padding: 1rem 2rem;
  border-radius: 9999px;
  font-size: 1.125rem;
  font-weight: 500;
  border: 1px solid rgba(160, 174, 192, 0.5);
  backdrop-filter: blur(5px);
  cursor: pointer;
  transition: all 0.3s ease;
  transform: scale(1);
  text-decoration: none;
}

.home-cta-button-secondary:hover {
  background: rgba(255, 255, 255, 0.2);
  transform: scale(1.05);
  text-decoration: none;
}

.home-cta-button-secondary:focus,
.home-cta-button-secondary:visited,
.home-cta-button-secondary:active {
  text-decoration: none;
}

/* ===== VIDEO SECTION ===== */
.home-video-section {
  padding: 6rem 2rem;
  position: relative;
  z-index: 2;
  width: 100%;
  box-sizing: border-box;
}

.home-video-content {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.home-video-container {
  position: relative;
  max-width: 900px;
  margin: 3rem auto 0;
  transition: transform 0.3s ease;
}

.home-video-container:hover {
  transform: translateY(-5px);
}

.home-laptop-frame {
  position: relative;
  width: 100%;
  height: auto;
  display: block;
}

.home-laptop-image {
  width: 100%;
  height: auto;
  display: block;
  z-index: 2;
  position: relative;
  pointer-events: none;
}

.home-video-screen {
  position: absolute;
  top: 20%;
  left: 22.5%;
  right: 23%;
  bottom: 30%;
  overflow: hidden;
  z-index: 1;
  background: #000;
  border-radius: 0.2rem;
}

.home-intro-video {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
  background: #000;
}

.home-video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3;
  pointer-events: none;
}

.home-play-button {
  width: 80px;
  height: 80px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  pointer-events: auto;
  transition: all 0.3s ease;
  animation: pulse 2s infinite;
}

.home-play-button:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: scale(1.1);
}

.home-play-icon {
  width: 0;
  height: 0;
  border-left: 20px solid white;
  border-top: 12px solid transparent;
  border-bottom: 12px solid transparent;
  margin-left: 4px;
}

@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* ===== PROJECTS SECTION ===== */
.home-projects-section {
  padding: 5rem 1.5rem;
}

.home-projects-content {
  max-width: 80rem;
  margin: 0 auto;
}

.home-section-title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
  margin-top: 0;
}

.home-section-description {
  font-size: 1rem;
  color: #d1d5db;
  margin-bottom: 2rem;
  max-width: 32rem;
  text-align: left;
}

.home-intro-description {
  font-size: 1rem;
  color: white;
  margin-bottom: 1rem;
  margin-top: 2rem;
  max-width: 32rem;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.home-projects-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.5rem;
  margin-bottom: 3rem;
}

.home-project-card {
  transition: all 0.3s ease;
}

.home-project-image {
  background-color: #e5e7eb;
  border-radius: 0.75rem;
  margin-bottom: 1rem;
  height: 18rem;
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
}

.home-project-card:hover .home-project-image {
  background-color: #d1d5db;
}

.home-project-info {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.home-project-type {
  color: #e26f8adf;
  font-weight: 500;
  font-size: 0.875rem;
}

.home-project-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
}

.home-project-description {
  color: #d1d5db;
  line-height: 1.6;
  margin: 0;
}

.home-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem;
}

.home-tech {
  padding: 0.25rem 0.75rem;
  background-color: #e5e7eb;
  border-radius: 9999px;
  font-size: 0.875rem;
  color: #374151;
}

.home-more-projects-container {
  text-align: center;
}

.home-more-projects-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background-color: #e26f8adf;
  color: white;
  padding: 1rem 2rem;
  border-radius: 9999px;
  font-size: 1.125rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  transform: scale(1);
  text-decoration: none;
}

.home-more-projects-button:hover {
  background-color: #000000;
  transform: scale(1.05);
  text-decoration: none;
}

.home-more-projects-button:focus,
.home-more-projects-button:visited,
.home-more-projects-button:active {
  text-decoration: none;
}

/* ===== CONTACT SECTION ===== */
.home-contact-section {
  padding: 5rem 1.5rem;
}

.home-contact-content {
  max-width: 80rem;
  margin: 0 auto;
}

.home-contact-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
  align-items: center;
}

.home-contact-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 2rem;
  line-height: 1.1;
  margin-top: 0;
  color: #1a1a1a;
}

.home-contact-info {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.home-contact-description {
  font-size: 1.125rem;
  color: white;
  line-height: 1.6;
  margin: 0;
}

.home-contact-links {
  display: flex;
  align-items: center;
  gap: 1rem;
  flex-wrap: wrap;
}

.home-email {
  font-size: 1.125rem;
  color: #2d2d2d;
  margin-right: 0.5rem;
  text-decoration: none;
}

.home-email:hover,
.home-email:focus,
.home-email:visited,
.home-email:active {
  text-decoration: none;
}

/* ===== FOOTER ===== */
.home-footer {
  padding: 2rem 1.5rem;
  border-top: 1px solid black;
  position: relative;
  z-index: 10;
}

.home-footer-content {
  max-width: 80rem;
  margin: 0 auto;
  text-align: center;
}

.home-copyright {
  color: black;
  margin: 0;
}

/* ===== RESPONSIVE DESIGN ===== */

/* Desktop (1024px and above) */
@media (min-width: 1024px) {
  .home-title {
    font-size: 5rem;
  }
  
  .home-section-title {
    font-size: 4rem;
  }
  
  .home-contact-title {
    font-size: 4rem;
  }
  
  .home-projects-grid {
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
  }
  
  .home-contact-grid {
    grid-template-columns: 1fr 1fr;
  }
}

/* Tablet (768px to 1023px) */
@media (min-width: 768px) and (max-width: 1023px) {
  .home-title {
    font-size: 4rem;
  }
  
  .home-section-title {
    font-size: 3rem;
  }
  
  .home-contact-title {
    font-size: 3rem;
  }
  
  .home-hero-section {
    padding-left: 3rem;
  }
  
  .home-navigation {
    gap: 1.5rem;
    padding: 0.75rem 1.25rem;
  }
  
  .home-nav-button {
    padding: 0.75rem 1rem;
    font-size: 0.875rem;
  }
  
  .home-projects-grid {
    gap: 2rem;
  }
  
  .home-project-image {
    height: 16rem;
  }
  
  .home-contact-grid {
    grid-template-columns: 1fr 1fr;
  }
}

/* Mobile (481px to 767px) */
@media (min-width: 481px) and (max-width: 767px) {
  .home-navigation {
    gap: 1rem;
    padding: 0.5rem 1rem;
  }
  
  .home-nav-button {
    padding: 0.5rem;
    font-size: 0.75rem;
  }
  
  .home-hero-section {
    padding-left: 1.5rem;
    justify-content: center;
  }
  
  .home-hero-content {
    text-align: center;
  }
  
  .home-designer-label {
    text-align: center;
  }
  
  .home-description {
    margin-left: auto;
    margin-right: auto;
  }
  
  .home-cta-buttons {
    justify-content: center;
    flex-wrap: wrap;
  }
  
  .home-title {
    font-size: 2.5rem;
  }
  
  .home-section-title {
    font-size: 2.5rem;
  }
  
  .home-contact-title {
    font-size: 2.5rem;
  }
  
  .home-projects-grid {
    gap: 1.5rem;
  }
  
  .home-project-image {
    height: 12rem;
    padding: 1rem;
  }
  
  .home-video-section {
    padding: 4rem 1rem;
  }
  
  .home-video-container {
    margin: 2rem auto 0;
  }
  
  .home-play-button {
    width: 60px;
    height: 60px;
  }
  
  .home-play-icon {
    border-left: 15px solid white;
    border-top: 9px solid transparent;
    border-bottom: 9px solid transparent;
  }
}

/* Small Mobile (480px and below) */
@media (max-width: 480px) {
  .home-navigation {
    gap: 0.5rem;
    padding: 0.5rem 0.75rem;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .home-nav-button {
    padding: 0.5rem 0.75rem;
    font-size: 0.7rem;
  }
  
  .home-hero-section {
    padding: 1rem;
    padding-top: 6rem;
  }
  
  .home-title {
    font-size: 2rem;
  }
  
  .home-section-title {
    font-size: 2rem;
  }
  
  .home-contact-title {
    font-size: 2rem;
  }
  
  .home-description {
    font-size: 1rem;
    margin-bottom: 2rem;
  }
  
  .home-cta-button {
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
  }
  
  .home-cta-button-secondary {
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
  }
  
  .home-projects-section {
    padding: 3rem 1rem;
  }
  
  .home-contact-section {
    padding: 3rem 1rem;
  }
  
  .home-project-image {
    height: 10rem;
  }
  
  .home-side-navigation {
    bottom: 1rem;
    right: 1rem;
    gap: 0.75rem;
  }
  
  .home-social-icon {
    padding: 0.5rem;
    font-size: 0.875rem;
  }
  
  /* Scroll indicator positioning handled by about.css */
  
  .home-video-section {
    padding: 3rem 0.5rem;
  }
  
  .home-video-container {
    margin: 1.5rem auto 0;
  }
  
  .home-play-button {
    width: 50px;
    height: 50px;
  }
  
  .home-play-icon {
    border-left: 12px solid white;
    border-top: 7px solid transparent;
    border-bottom: 7px solid transparent;
  }
}