/* Sheytoon Project Page Styles */

/* Container & Background */
.sheytoon-project-container {
  min-height: 100vh;
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
  background: linear-gradient(135deg, 
    #f8f6f7 0%,
    #f2f0f1 25%,
    #ede9eb 50%,
    #f0ecee 75%,
    #f5f3f4 100%);
}

/* Scroll Indicator - Using unified about-scroll-indicator from about.css */



/* Main Content */
.sheytoon-project-main {
  position: relative;
  z-index: 2;
  padding: 8rem 2rem 4rem;
  max-width: 80rem;
  margin: 0 auto;
}

/* Project Header */
.sheytoon-project-header {
  text-align: center;
  margin-bottom: 4rem;
}

.sheytoon-project-title {
  font-size: 3.5rem;
  font-weight: bold;
  color: #1a1a1a;
  margin-bottom: 1rem;
  line-height: 1.2;
}

.sheytoon-project-subtitle {
  font-size: 1.25rem;
  color: #6b7280;
  font-weight: 500;
}

/* Section Styles */
.sheytoon-section {
  margin-bottom: 5rem;
}

.sheytoon-section-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

@media (max-width: 768px) {
  .sheytoon-section-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

/* Text Content */
.sheytoon-text-content {
  background: rgba(255, 255, 255, 0.9);
  padding: 2.5rem;
  border-radius: 1rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.sheytoon-section-title {
  font-size: 2rem;
  font-weight: bold;
  color: #1a1a1a;
  margin-bottom: 1.5rem;
}

.sheytoon-section-description {
  font-size: 1.125rem;
  line-height: 1.7;
  color: #4b5563;
}

/* Image Container */
.sheytoon-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Figma Embed Styling */
.figma-embed-wrapper {
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  height: 400px;
  width: 100%;
}

.figma-embed-wrapper:hover {
  transform: scale(1.02);
}

.figma-embed-wrapper iframe {
  border-radius: 1rem;
  width: 100%;
  height: 100%;
  border: none;
}

.sheytoon-project-image {
  max-width: 100%;
  height: auto;
  border-radius: 1rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.sheytoon-project-image:hover {
  transform: scale(1.02);
}

/* Back Button */
.sheytoon-back-button-container {
  text-align: center;
  margin-top: 4rem;
}

.sheytoon-back-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.9);
  color: #1a1a1a;
  padding: 1rem 2rem;
  border-radius: 9999px;
  font-size: 1.125rem;
  font-weight: 500;
  text-decoration: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.sheytoon-back-button:hover {
  background: #1a1a1a;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (max-width: 768px) {
  .sheytoon-project-main {
    padding: 6rem 1rem 3rem;
  }
  
  .sheytoon-project-title {
    font-size: 2.5rem;
  }
  
  .sheytoon-project-subtitle {
    font-size: 1.125rem;
  }
  
  .sheytoon-section-title {
    font-size: 1.75rem;
  }
  
  .sheytoon-text-content {
    padding: 2rem;
  }
  
  .sheytoon-section-content {
    gap: 2rem;
  }
}

/* Ensure side navigation is visible */
.home-side-navigation,
.home-social-icon {
  z-index: 99999 !important;
  pointer-events: auto !important;
}








