/* About Page Styles */

/* Container & Theme */
.about-container {
  min-height: 100vh;
  transition: all 0.5s ease;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  overflow-x: hidden;
  background: linear-gradient(135deg, 
    #f8f6f7 0%,
    #f2f0f1 25%,
    #ede9eb 50%,
    #f0ecee 75%,
    #f5f3f4 100%);
  color: #1a1a1a;
}

/* Fixed Header */
.about-header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
  padding: 1.5rem 1.5rem 1.5rem 1rem;
}

.about-header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 80rem;
  margin: 0 auto;
}

.about-logo {
  font-size: 1.5rem;
  font-weight: bold;
}

/* Override home logo size for about page to keep it smaller */
.about-container .home-logo-image {
  width: 3rem;
  height: 3rem;
  object-fit: contain;
  border-radius: 0.5rem;
  transition: transform 0.3s ease;
}

.about-container .home-logo-image:hover {
  transform: scale(1.05);
}

/* Navigation */
.about-navigation {
  display: flex;
  align-items: center;
  gap: 2rem;
  background: rgba(130, 130, 130, 0.3);
  backdrop-filter: blur(10px);
  color: white;
  padding: 0.75rem 1.5rem;
  border-radius: 9999px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.about-nav-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 9999px;
  background: none;
  border: none;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.875rem;
  font-weight: 500;
  text-decoration: none;
}

.about-nav-button:hover,
.about-nav-button.active {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  text-decoration: none;
}

.about-nav-button:focus,
.about-nav-button:visited,
.about-nav-button:active {
  text-decoration: none !important;
  outline: none;
}

.about-dot {
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
}

.blue-dot {
  background-color: #e26f8adf;
}

.pink-dot {
  background-color: #C1B6BA;
}

.yellow-dot {
  background-color: #63a4d1;
}

/* Side Navigation */
.about-side-navigation {
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  z-index: 99999;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  pointer-events: auto;
}

.about-social-icon {
  display: block;
  padding: 0.75rem;
  background: rgba(130, 130, 130, 0.5);
  backdrop-filter: blur(15px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  border-radius: 0.5rem;
  transition: all 0.3s ease;
  transform: scale(1);
  text-decoration: none !important;
  z-index: 99999;
  pointer-events: auto;
}

.about-social-icon:hover {
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(15px);
  transform: scale(1.1);
  text-decoration: none !important;
}

.about-social-icon:focus,
.about-social-icon:visited,
.about-social-icon:active {
  text-decoration: none !important;
}

.about-social-icon.linkedin:hover {
  background: rgba(37, 99, 235, 0.6);
  backdrop-filter: blur(15px);
}

.about-social-icon.email:hover {
  background: rgba(5, 150, 105, 0.6);
  backdrop-filter: blur(15px);
}

.about-social-icon.instagram:hover {
  background: rgba(219, 39, 119, 0.6);
  backdrop-filter: blur(15px);
}

/* Dark Mode Toggle */
.about-dark-mode-toggle {
  position: fixed;
  bottom: 1.5rem;
  right: 1.5rem;
  z-index: 50;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem;
  background-color: #374151;
  color: white;
  border-radius: 9999px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.about-dark-mode-toggle:hover {
  background-color: #4b5563;
}

.about-drk-text {
  font-size: 0.75rem;
  margin-left: 0.5rem;
}

/* Scroll Indicator */
.about-scroll-indicator {
  position: fixed;
  bottom: 50%;
  right: 3rem;
  transform: translateY(50%);
  z-index: 99999999;
  cursor: pointer;
  animation: bounce 2s infinite;
  pointer-events: auto !important;
}

.about-scroll-arrow {
  color: #6b7280;
  transition: color 0.3s ease;
}

.about-scroll-indicator:hover .about-scroll-arrow {
  color: #3b82f6;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(50%);
  }
  40% {
    transform: translateY(40%);
  }
  60% {
    transform: translateY(45%);
  }
}

/* Floating Pearls */
.about-floating-pearls {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  overflow: hidden;
  z-index: 1;
}

.about-pearl {
  position: absolute;
  opacity: 0.8;
  border-radius: 50%;
  filter: blur(0.3px);
  box-shadow: 
    0 8px 32px rgba(255, 255, 255, 0.15),
    inset 0 4px 16px rgba(255, 255, 255, 0.3),
    inset 0 -4px 16px rgba(200, 180, 190, 0.2);
  transition: all 0.6s ease-out;
  background: radial-gradient(circle at 30% 30%, 
    rgba(255, 255, 255, 0.8) 0%,
    rgba(240, 220, 230, 0.6) 30%,
    rgba(220, 200, 215, 0.4) 70%,
    rgba(200, 180, 200, 0.3) 100%);
  cursor: none;
  will-change: transform;
  z-index: 9999;
}

.about-pearl-1 {
  top: 12%;
  left: 5%;
  width: 10rem;
  height: 10rem;
}

.about-pearl-2 {
  top: 20%;
  right: 18%;
  width: 6rem;
  height: 6rem;
}

.about-pearl-3 {
  bottom: 35%;
  left: 3%;
  width: 4rem;
  height: 4rem;
}

.about-pearl-4 {
  bottom: 25%;
  right: 15%;
  width: 14rem;
  height: 14rem;
}

.about-pearl-5 {
  top: 65%;
  left: 20%;
  width: 8rem;
  height: 8rem;
}

.about-pearl-6 {
  top: -4%;
  left: 35%;
  width: 4rem;
  height: 4rem;
}

/* Hero Section */
.about-hero-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  position: relative;
  z-index: 10;
}

.about-hero-content {
  text-align: center;
  max-width: 64rem;
  margin: 0 auto;
}

.about-intro {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
  animation: slideInFromBottom 1.2s ease-out both;
}

.about-label {
  font-size: 1.5rem;
  font-weight: 500;
  color: #6b7280;
}

.about-emoji {
  font-size: 1.5rem;
}

.about-greeting {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 1rem;
  line-height: 1.1;
  margin-top: 0;
  color: #000000;
  animation: slideInFromBottom 1.2s ease-out 0.4s both, sparkle 3s ease-in-out infinite;
  position: relative;
}

.about-rotating-text {
  font-size: 4rem;
  font-weight: bold;
  line-height: 1.1;
  margin: 0;
  opacity: 1;
  transform: translateY(0);
  color: #000000;
  animation: slideInFromBottom 1.2s ease-out 0.8s both, naturalBreathing 4s ease-in-out 2s infinite;
  height: 4.5rem;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.about-accent {
  color: #3b82f6;
}

/* Slide up animation keyframes */
@keyframes slideUpOut {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  100% {
    transform: translateY(-60px);
    opacity: 0;
  }
}

@keyframes slideUpIn {
  0% {
    transform: translateY(60px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

/* Apply animations to text states */
.about-rotating-text.fade-out {
  animation: slideUpOut 0.15s ease-out forwards;
}

.about-rotating-text.fade-in {
  animation: slideUpIn 0.15s ease-out forwards;
}

/* Natural breathing animation for the text */
@keyframes naturalBreathing {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.02);
    opacity: 0.95;
  }
}

/* Gentle page load animations */
@keyframes slideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Typing effect keyframes */
@keyframes typeIn {
  0% {
    width: 0;
    opacity: 0;
  }
  1% {
    opacity: 1;
  }
  100% {
    width: 100%;
    opacity: 1;
  }
}

/* Removed conflicting pearl animations to prevent circular movement */

/* Profile Section */
.about-profile-section {
  min-height: 100vh;
  display: flex;
  align-items: center;
  padding: 5rem 1.5rem;
  position: relative;
  z-index: 10;
}

.about-profile-content {
  max-width: 80rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 4rem;
  align-items: center;
}

@media (min-width: 1024px) {
  .about-profile-content {
    grid-template-columns: 1fr 1.5fr;
  }
}

.about-profile-image-container {
  position: relative;
  background: #d5beb5;
  border-radius: 2rem;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-profile-image {
  width: 100%;
  max-width: 400px;
  height: auto;
  border-radius: 1rem;
  object-fit: cover;
}

.about-profile-info {
  /* spacing handled by individual elements */
}

.about-profile-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 2rem;
  margin-top: 0;
}

.about-profile-text {
  margin-bottom: 3rem;
}

.about-profile-text p {
  font-size: 1.125rem;
  line-height: 1.7;
  color: #6b7280;
  margin-bottom: 1.5rem;
}

.about-resume-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: #d5beb5;
  color: white;
  padding: 1rem 2rem;
  border-radius: 9999px;
  font-size: 1.125rem;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s ease;
  transform: scale(1);
}

.about-resume-button:hover {
  background: #000000;
  color: white;
  transform: scale(1.05);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

/* Skills Section */
.about-skills-section {
  padding: 5rem 1.5rem;
  position: relative;
  z-index: 10;
}

.about-skills-content {
  max-width: 80rem;
  margin: 0 auto;
}

.about-skills-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 3rem;
  text-align: center;
}

.about-skills-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

@media (min-width: 768px) {
  .about-skills-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.about-skill-category {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(15px);
  padding: 2rem;
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 0.3s ease;
  transform: translateY(0);
}

.about-skill-category:hover {
  background: rgba(255, 255, 255, 0.9);
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.about-skill-category h3 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #63a4d1;
}

.about-skill-category p {
  color: #6b7280;
  line-height: 1.6;
}

.about-skill-category ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.about-skill-category li {
  color: #6b7280;
  line-height: 1.6;
  margin-bottom: 0.5rem;
  position: relative;
  padding-left: 1rem;
}

.about-skill-category li:before {
  content: "â€¢";
  color: #63a4d1;
  font-weight: bold;
  position: absolute;
  left: 0;
}

/* Values Section */
.about-values-section {
  padding: 5rem 1.5rem;
  position: relative;
  z-index: 10;
}

.about-values-content {
  max-width: 80rem;
  margin: 0 auto;
}

.about-values-title {
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 3rem;
  text-align: center;
}

.about-values-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}

@media (min-width: 768px) {
  .about-values-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.about-value-item {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(15px);
  padding: 2rem;
  border-radius: 1rem;
  border: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 0.3s ease;
  transform: translateY(0);
}

.about-value-item:hover {
  background: rgba(255, 255, 255, 0.9);
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.about-value-item h3 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: #e26f8adf;
}

.about-value-item p {
  color: #6b7280;
  line-height: 1.6;
}

/* Contact Section on Top */
.home-contact-section {
  position: relative;
  z-index: 9999;
}

.home-contact-content {
  position: relative;
  z-index: 9999;
}

/* About Page Contact Description */
.about-container .home-contact-description {
  color: #6b7280 !important;
}

/* Responsive Design */
@media (min-width: 768px) {
  .about-greeting,
  .about-rotating-text {
    font-size: 5rem;
  }
  
  .about-profile-title,
  .about-skills-title,
  .about-values-title {
    font-size: 4rem;
  }
}

@media (max-width: 768px) {
  .about-navigation {
    gap: 1rem;
    padding: 0.5rem 1rem;
  }
  
  .about-nav-button {
    padding: 0.5rem;
    font-size: 0.75rem;
  }
  
  .about-greeting,
  .about-rotating-text {
    font-size: 3rem;
  }
  
  .about-profile-title,
  .about-skills-title,
  .about-values-title {
    font-size: 2.5rem;
  }
  
  .about-pearl-1,
  .about-pearl-2,
  .about-pearl-3,
  .about-pearl-4 {
    width: 4rem;
    height: 4rem;
  }
}

/* Pink Sparkle Effects */
@keyframes sparkle {
  0%, 100% {
    text-shadow: 
      0 0 5px rgba(213, 190, 181, 0.8),
      0 0 10px rgba(213, 190, 181, 0.6),
      0 0 15px rgba(213, 190, 181, 0.4),
      0 0 20px rgba(213, 190, 181, 0.3);
  }
  50% {
    text-shadow: 
      0 0 8px rgba(213, 190, 181, 1),
      0 0 15px rgba(213, 190, 181, 0.9),
      0 0 25px rgba(213, 190, 181, 0.7),
      0 0 35px rgba(213, 190, 181, 0.5),
      0 0 45px rgba(213, 190, 181, 0.3);
  }
}

/* About Page Footer */
.about-container .home-footer {
  border-top: 1px solid black;
}

.about-container .home-copyright {
  color: black;
}

