/* Pearl Beauty Project Page Styles */

/* Container & Background */
.pearl-beauty-project-container {
  min-height: 100vh;
  position: relative;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  overflow-x: hidden;
  overflow-y: auto;
  background: linear-gradient(135deg, 
    #f8f6f7 0%,
    #f2f0f1 25%,
    #ede9eb 50%,
    #f0ecee 75%,
    #f5f3f4 100%);
}

/* Scroll Indicator - Using unified about-scroll-indicator from about.css */



/* Main Content */
.pearl-beauty-project-main {
  position: relative;
  z-index: 2;
  padding: 8rem 2rem 4rem;
  max-width: 80rem;
  margin: 0 auto;
}

/* Project Header */
.pearl-beauty-project-header {
  text-align: center;
  margin-bottom: 4rem;
}

.pearl-beauty-project-title {
  font-size: 3.5rem;
  font-weight: bold;
  color: #1a1a1a;
  margin-bottom: 1rem;
  line-height: 1.2;
}

.pearl-beauty-project-subtitle {
  font-size: 1.25rem;
  color: #6b7280;
  font-weight: 500;
}

.pearl-beauty-project-title {
  font-size: 3.5rem;
  font-weight: bold;
  color: #1a1a1a;
  margin-bottom: 1rem;
  line-height: 1.2;
}

.pearl-beauty-project-subtitle {
  font-size: 1.25rem;
  color: #6b7280;
  font-weight: 500;
}

/* Section Styles */
.pearl-beauty-section {
  margin-bottom: 5rem;
}

.pearl-beauty-section-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
}

@media (max-width: 768px) {
  .pearl-beauty-section-content {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
}

/* Text Content */
.pearl-beauty-text-content {
  background: rgba(255, 255, 255, 0.9);
  padding: 2.5rem;
  border-radius: 1rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.pearl-beauty-section-title {
  font-size: 2rem;
  font-weight: bold;
  color: #1a1a1a;
  margin-bottom: 1.5rem;
}

.pearl-beauty-section-description {
  font-size: 1.125rem;
  line-height: 1.7;
  color: #4b5563;
}

/* Image Container */
.pearl-beauty-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.pearl-beauty-project-image {
  max-width: 100%;
  height: auto;
  border-radius: 1rem;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease;
}

.pearl-beauty-project-image:hover {
  transform: scale(1.02);
}

/* Back Button */
.pearl-beauty-back-button-container {
  text-align: center;
  margin-top: 4rem;
}

.pearl-beauty-back-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: rgba(255, 255, 255, 0.9);
  color: #1a1a1a;
  padding: 1rem 2rem;
  border-radius: 9999px;
  font-size: 1.125rem;
  font-weight: 500;
  text-decoration: none;
  border: 1px solid rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}

.pearl-beauty-back-button:hover {
  background: #1a1a1a;
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.2);
}

/* Responsive Design */
@media (max-width: 768px) {
  .pearl-beauty-project-main {
    padding: 6rem 1rem 3rem;
  }
  
  .pearl-beauty-project-title {
    font-size: 2.5rem;
  }
  
  .pearl-beauty-project-subtitle {
    font-size: 1.125rem;
  }
  
  .pearl-beauty-section-title {
    font-size: 1.75rem;
  }
  
  .pearl-beauty-text-content {
    padding: 2rem;
  }
  
  .pearl-beauty-section-content {
    gap: 2rem;
  }
}

/* Ensure side navigation is always on top */
.home-side-navigation {
  position: fixed !important;
  bottom: 1.5rem !important;
  right: 1.5rem !important;
  left: auto !important;
  z-index: 999999 !important;
  display: flex !important;
  flex-direction: column !important;
  gap: 1rem !important;
  pointer-events: auto !important;
}

.home-social-icon {
  display: block !important;
  padding: 0.75rem !important;
  background: rgba(55, 65, 81, 0.3) !important;
  backdrop-filter: blur(10px) !important;
  border: 1px solid rgba(255, 255, 255, 0.1) !important;
  color: white !important;
  border-radius: 0.5rem !important;
  transition: all 0.3s ease !important;
  transform: scale(1) !important;
  text-decoration: none !important;
  z-index: 999999 !important;
  pointer-events: auto !important;
}

.home-social-icon:hover {
  transform: scale(1.1) !important;
  text-decoration: none !important;
}

.home-social-icon.linkedin:hover {
  background: rgba(37, 99, 235, 0.6) !important;
  backdrop-filter: blur(15px) !important;
}

.home-social-icon.email:hover {
  background: rgba(5, 150, 105, 0.6) !important;
  backdrop-filter: blur(15px) !important;
}

.home-social-icon.instagram:hover {
  background: rgba(219, 39, 119, 0.6) !important;
  backdrop-filter: blur(15px) !important;
}

/* Contact section description styling for Pearl Beauty page */
.pearl-beauty-project-container .home-contact-description {
  color: #9ca3af !important;
  font-size: 1.125rem;
  line-height: 1.6;
}








