/* Lumora Project Page Styles */

/* Global Settings */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Ensure side navigation is always on top */
.home-side-navigation {
  z-index: 99999 !important;
  pointer-events: auto !important;
}

.home-social-icon {
  z-index: 99999 !important;
  pointer-events: auto !important;
}

.lumora-project-container {
  min-height: 100vh;
  position: relative;
  overflow-x: hidden;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  background: linear-gradient(135deg, 
    #f8f6f7 0%,
    #f2f0f1 25%,
    #ede9eb 50%,
    #f0ecee 75%,
    #f5f3f4 100%);
}



/* Ensure content appears above overlay */
.lumora-project-container > * {
  position: relative;
  z-index: 2;
}

/* Main Content */
.lumora-project-main {
  padding-top: 4rem;
  padding-bottom: 4rem;
  max-width: 80rem;
  margin: 0 auto;
  padding-left: 2rem;
  padding-right: 2rem;
}

/* Project Header */
.lumora-project-header {
  text-align: center;
  margin-bottom: 6rem;
}

.lumora-project-title {
  font-size: 3.5rem;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 1rem;
  margin-top: 0;
  font-family: serif;
}

.lumora-project-subtitle {
  font-size: 1.25rem;
  color: #6b7280;
  font-weight: 500;
}

/* Sections */
.lumora-section {
  margin-bottom: 8rem;
}

.lumora-section-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  align-items: center;
  padding: 2rem 0;
}

.lumora-section-content.reverse {
  direction: rtl;
}

.lumora-section-content.reverse > * {
  direction: ltr;
}

/* Text Content */
.lumora-text-content {
  padding: 2rem;
}

.lumora-section-title {
  font-size: 2.5rem;
  font-weight: bold;
  color: #1f2937;
  margin-bottom: 2rem;
  margin-top: 0;
  line-height: 1.2;
  font-family: serif;
}

.lumora-section-description {
  font-size: 1.125rem;
  color: #6b7280;
  line-height: 1.7;
  margin-bottom: 2rem;
  max-width: 32rem;
}

/* Demo Button */
.lumora-demo-link {
  margin-top: 2rem;
}

.lumora-demo-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: transparent;
  color: #000000;
  padding: 0.5rem 0;
  font-size: 1.25rem;
  font-weight: bold;
  font-family: serif;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
}

.lumora-demo-button::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 28px;
  width: calc(100% - 28px);
  height: 3px;
  background: #e26f8adf;
}

.lumora-demo-button svg {
  color: #9ca3af;
  transition: color 0.3s ease;
}

.lumora-demo-button:hover svg {
  color: #6b7280;
}

.lumora-demo-button:hover {
  color: #1f2937;
  text-decoration: none;
}

/* Image Content */
.lumora-image-content {
  position: relative;
}

.lumora-image-container {
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  height: 400px;
}

.lumora-image-container:hover {
  transform: scale(1.02);
}

.lumora-project-image {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: contain;
}

/* Figma Embed Styling */
.figma-embed-wrapper {
  position: relative;
  border-radius: 1rem;
  overflow: hidden;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  height: 400px;
  width: 100%;
}

.figma-embed-wrapper:hover {
  transform: scale(1.02);
}

.figma-embed-wrapper iframe {
  border-radius: 1rem;
  width: 100%;
  height: 100%;
  border: none;
}

/* Scroll Indicator - Using unified about-scroll-indicator from about.css */

/* Back Button */
.lumora-back-button-container {
  text-align: center;
  margin-top: 4rem;
  margin-bottom: 2rem;
}

.lumora-back-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  background: white;
  color: #1f2937;
  padding: 1rem 2rem;
  border-radius: 9999px;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  transform: scale(1);
}

.lumora-back-button:hover {
  background: #1f2937;
  color: white;
  transform: scale(1.05);
  box-shadow: 0 10px 30px rgba(31, 41, 55, 0.3);
  text-decoration: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  .lumora-project-main {
    padding-top: 6rem;
    padding-left: 1rem;
    padding-right: 1rem;
  }
  
  .lumora-project-title {
    font-size: 2.5rem;
  }
  
  .lumora-section-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    text-align: center;
  }
  
  .lumora-section-content.reverse {
    direction: ltr;
  }
  
  .lumora-section-title {
    font-size: 2rem;
  }
  
  .lumora-section-description {
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .lumora-project-title {
    font-size: 2rem;
  }
  
  .lumora-section-title {
    font-size: 1.75rem;
  }
  
  .lumora-section-description {
    font-size: 0.95rem;
  }
}


